'use client';
import { useSearchParams } from 'next/navigation';

export default function FlashMessage() {
  //クエリ文字列を取得
  const searchParams = useSearchParams();
  // パラメータを取得
  const success = searchParams.get('success');
  const error = searchParams.get('error');

  if (!success && !error) return null;
  let message;

  switch (success) {
    case '1':
      message = 'ログインしました';
      break;
    case '2':
      message = '新規投稿しました';
      break;
    case '3':
      message = 'ログアウトしました';
      break;
  }

  switch (error) {
    case '1':
      message = 'ログインしてください';
      break;
  }

  return (
    <>
      {message && (
        <div className="max-w-7xl mx-auto px-4 py-2 bg-green-100 text-black rounded-md shadow">
          {message}
        </div>
      )}
    </>
  );
}
